:root {
  --main-bg-color: #03a9f4;
  --main-color: #fff;
  --main-link-color: #fff;
  --main-link-hover-color: #eceff1;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  font-family: "Montserrat", sans-serif;
  background: var(--main-bg-color);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

h1 {
  font-size: 3em;
  line-height: 1.05em;
}

h4 {
  font-size: 1.125em;
  line-height: 1.222222222em;
}

a {
  color: var(--main-link-color);
}

a:hover {
  color: var(--main-link-hover-color);
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  padding: 0px 20px;
  color: var(--main-color);
}

.title {
  word-break: break-word;
}

.phrase {
  margin-top: 12px;
  word-break: break-word;
}

.icons {
  display: block;
  margin-top: 24px;
}

.icon {
  font-size: 2rem;
}

.icon:not(:first-child) {
  margin-left: 10px;
}
